<template>
    <main id="opu" class="p-inputtext-sm p-input-filled">
        <opu-top-form />

        <opu-drug-chart />

        <opu-middle-form />

        <opu-patient-chart type="pre" />

        <opu-bottom-form />

        <opu-patient-chart type="post" />

        <p-toolbar class="p-p-2 p-shadow-2">
            <template #left>
                <p-button
                    label="Save"
                    class="p-mr-2"
                    @click="save"
                    :disabled="saveDisable"
                />
                <p-button
                    label="PDF Report"
                    class="p-mr-2"
                    @click="openPdfReport"
                />
                <p-button label="Delete" class="p-mr-2" @click="deleteOpu" />
            </template>
        </p-toolbar>

        <born-oocyte-collection v-if="isBornCycle" />
    </main>
</template>

<script>
import { computed, onMounted, onUnmounted, provide, ref } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { useLoadIinitalFormData, useOpuForm } from "@/composable/opu";

import { useRouter } from "vue-router";
import { useToast } from "primevue/usetoast";

import OpuDrugChart from "@/components/opu/drug-chart.vue";
import OpuPatientChart from "@/components/opu/patient-chart.vue";
import OpuMiddleForm from "@/components/opu/middle-form.vue";
import OpuBottomForm from "@/components/opu/bottom-form.vue";
import OpuTopForm from "@/components/opu/top-form.vue";
import BornOocyteCollection from "@/components/born/oocyte-collection-form.vue";
import { serialize } from "php-serialize";
import { buildPdfUrl } from "@/service/utils";

export default {
    components: {
        OpuDrugChart,
        OpuPatientChart,
        OpuMiddleForm,
        OpuBottomForm,
        OpuTopForm,
        BornOocyteCollection
    },
    setup() {
        const store = useStore();
        const toast = useToast();

        const opuForm = useOpuForm();
        provide("opuForm", opuForm);

        const options = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" }
        ]);

        const saveDisable = computed(() => opuForm.docDate == null);
        const save = () => {
            if (opuForm.cycleId == null) {
                toast.add({
                    severity: "error",
                    summary: "Error!!!",
                    detail: "Please select cycle first",
                    life: 3000
                });
                return;
            }

            emitter.emit("app:show-page-loading", true);
            store
                .dispatch("opu/saveSummary", { ...opuForm })
                .then((data) => {
                    getOpuSummary(data.cycle_id);
                })
                .catch((error) => {
                    emitter.emit("app:show-page-loading", false);
                });
        };

        const onSummaryLoaded = useLoadIinitalFormData({ opuForm, store });
        const getOpuSummary = (cycleId) => {
            emitter.emit("app:show-page-loading", true);
            store
                .dispatch("opu/getOPUSummary", cycleId)
                .then(() => {
                    emitter.emit("opu:summary-loaded");
                    emitter.emit("app:show-page-loading", false);
                })
                .catch((error) => {
                    emitter.emit("opu:summary-loaded");
                    emitter.emit("app:show-page-loading", false);
                });
        };

        const isBornCycle = computed(
            () => store.getters["artCycle/isBornCycle"]
        );

        const router = useRouter();
        const deleteOpu = () => {
            store
                .dispatch("opu/deleteOpu")
                .then(() => {
                    toast.add({
                        severity: "success",
                        summary: "Success!",
                        detail: "OPU Deleted Succesfully",
                        life: 3000
                    });
                    location.reload();
                })
                .catch(() => {
                    toast.add({
                        severity: "error",
                        summary: "Error!",
                        detail: "Somethig went wrong",
                        life: 3000
                    });
                });
        };

        const openPdfReport = () => {
            let params = {
                cycleId: store.state.artCycle.selectedCycle.cycleId
            };

            let url = buildPdfUrl("OPUReport", params);
            window.open(url);
        };

        onMounted(() => {
            emitter.on("opu:summary-loaded", onSummaryLoaded);

            if (!store.state.artCycle.selectedCycle) {
                toast.add({
                    severity: "warn",
                    summary: "Warnning!",
                    detail: "Please select cycle first",
                    life: 3000
                });
                return;
            }

            getOpuSummary(store.state.artCycle.selectedCycle.cycleId);
        });

        onUnmounted(() => {
            emitter.off("opu:summary-loaded", onSummaryLoaded);
        });

        return {
            options,
            save,
            saveDisable,
            isBornCycle,
            deleteOpu,
            openPdfReport
        };
    }
};
</script>

<style>
#opu .p-card .p-card-content {
    padding: 0 0;
}

#opu .p-field {
    margin-bottom: 0.5rem;
}

#opu .p-card {
    height: 100%;
}

#opu table .p-inputtext {
    width: -webkit-fill-available;
}

#opu .p-inputnumber {
    height: 24.5px;
}
</style>
