<script>
import { useOocyteCollectionInit } from "@/composable/born";
import { onMounted, watch } from "vue-demi";

export default {
    setup() {
        const {
            store,
            form,
            loading,
            save,
            fillForm,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions
        } = useOocyteCollectionInit();

        onMounted(() => {
            if (store.state.born.oocyteCollections == null) {
                store
                    .dispatch(
                        "born/getBronV2",
                        store.state.artCycle.selectedCycle
                    )
                    .then(() => {
                        fillForm(store.state.born.oocyteCollections);
                    });
            } else {
                fillForm(store.state.born.oocyteCollections);
            }
        });

        return {
            form,
            loading,
            save,
            fillForm,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions
        };
    }
};
